









































































export default {
    name: "td-main"
    props: ['itemIndex']
    computed:
        item: (vm) ->
            @hItem = vm.$store.state.selected.catalog.data.todoList[vm.itemIndex]
            @loadInit(@hItem)
            return;

    data: ->
        hItem: null
        repeats: null
        create:
            tag: ''
        time: null
        date: null
        note: null
        chips: null
        rows: 2
        rowsMax: 7
        repeatTerm: [
            icon: '单',
            label: '单次'
        ,
            icon: '天',
            label: '每天'
        ,
            icon: '周',
            label: '每周'
        ,
            icon: '月',
            label: '每月'
        ,
            icon: '年',
            label: '每年'
        ,
            icon: '自',
            label: '自定义'
        ]
    mounted: ->

    methods:
        setTime: (type) ->
            if type is 'time'
                @setValue type, @time.toJSON()
            if type is 'date'
                @setValue type, @date.toJSON()
            if @date && @time
                datetime = new Date(@date.getFullYear(),
                        @date.getMonth(),
                        @date.getDate(),
                        @time.getHours(),
                        @time.getMinutes(),
                        @time.getSeconds())
                mtd = Object.assign({}, @hItem)
                mtd.datetime = datetime;
                @$emit('pushNotify', mtd)
        setValue: (name, value)->
            @$store.dispatch 'saveTodoListChange',
                prop: [@itemIndex, name]
                value: value

        resizeNoteArea: ->
            notetext = @$refs.notearea.$el
            lineHeight = window.getComputedStyle(notetext, null).getPropertyValue('line-height')
            lineHeight = Number(lineHeight.substring(0, lineHeight.indexOf('px')))
            @rows = Math.floor((@$el.scrollHeight - 58) / lineHeight)
            console.log 'note resize area', @rows, lineHeight, @$el.scrollHeight
            notediv = @$refs.notearea.$children[0]
            @item.note = '描述一下详细情况～'
            return

        loadInit: (e) ->
            console.log 'load item', e
            if e.time?
                @time = new Date(e.time)
            if e.date?
                @date = new Date(e.date)
            @note = e.note
            @repeats = e.repeats
            @chips = e.tags

        addTag: () ->
            return if @create.tag is ''
            @chips.push
                label: @create.tag
                color: 'primary'
            @create.tag = ''
            @setValue 'tags', @chips

}
